"use client"
import React, { useState } from "react";
import {
  Card,
  CardMedia,
  CardContent,
  Typography,
  Grid,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Box,
  Button,
} from "@mui/material";

const properties = [
  {
    id: 1,
    title: "Diamond Manor Apartment",
    type: "For Rent",
    address: "145 Willow St #2, Cambridge, MA 02141",
    beds: 2,
    baths: 2,
    sqft: 0,
    dateAvailable: "12/10/2025",
    image: "https://via.placeholder.com/300x200", // Replace with real image URLs
  },
  {
    id: 2,
    title: "Villa Bay",
    type: "For Sale",
    address: "145 Willow St #2, Cambridge, MA 02141",
    beds: 2,
    baths: 2,
    sqft: 150,
    image: "https://via.placeholder.com/300x200", // Replace with real image URLs
  },
];

const HomeCards = () => {
  const [filterType, setFilterType] = useState("All");

  const handleFilterChange = (event) => {
    setFilterType(event.target.value);
  };

  const filteredProperties = properties.filter((property) =>
    filterType === "All" ? true : property.type === filterType
  );

  return (
    <Box sx={{ padding: 3 }}>
      <Box sx={{ display: "flex", justifyContent: "space-between", mb: 3 }}>
        <FormControl variant="outlined" sx={{ minWidth: 200 }}>
          <InputLabel>Filter by Type</InputLabel>
          <Select
            value={filterType}
            onChange={handleFilterChange}
            label="Filter by Type"
          >
            <MenuItem value="All">All</MenuItem>
            <MenuItem value="For Rent">For Rent</MenuItem>
            <MenuItem value="For Sale">For Sale</MenuItem>
          </Select>
        </FormControl>
        <Button variant="contained" color="primary">
          Save Search
        </Button>
      </Box>
      <Grid container spacing={3}>
        {filteredProperties.map((property) => (
          <Grid item xs={12} sm={6} md={4} key={property.id}>
            <Card>
              <CardMedia
                component="img"
                height="140"
                image={property.image}
                alt={property.title}
              />
              <CardContent>
                <Typography variant="h6">{property.title}</Typography>
                <Typography variant="body2" color="textSecondary">
                  {property.address}
                </Typography>
                <Typography variant="body2">
                  Beds: {property.beds} | Baths: {property.baths} | Sqft:{" "}
                  {property.sqft}
                </Typography>
                {property.type === "For Rent" && (
                  <Typography variant="body2" color="textSecondary">
                    Available: {property.dateAvailable}
                  </Typography>
                )}
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default HomeCards;
